@font-face {
  font-family: 'N26';
  src: url("./fonts/Calibre-Regular.woff2") format("woff2"), url("./fonts/Calibre-Regular.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'N26';
  src: url("./fonts/Calibre-Medium.woff2") format("woff2"), url("./fonts/Calibre-Medium.woff") format("woff");
  font-weight: 600;
