.Results {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
  max-width: 100%;
  height: calc(100vh - 55px);
  padding: 0 20px; }
  .Results .text-field {
    font-family: "N26", sans-serif;
    color: #1b1b1b;
    letter-spacing: -0.4px;
    text-align: left; }
  .Results h1 {
    font-family: "N26", sans-serif;
    color: #1b1b1b;
    font-size: 42px;
    line-height: 1;
    margin-bottom: 7px; }
  .Results h2 {
    font-size: 26px;
    line-height: 1;
    margin-bottom: 18px; }
  .Results h3 {
    font-size: 19px;
    line-height: 1.4;
    opacity: 0.8;
    font-weight: normal;
    margin-bottom: 20px; }

@media (min-width: 860px) {
  .Results {
    width: 768px;
    margin: auto; }
    .Results:after {
      content: '';
      background-image: url("./img/results.jpg");
      background-repeat: no-repeat;
      background-position: bottom 0 right 0;
      background-size: cover;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: -1; } }



